<template>
  <div class="bigFileUpload">
    <input type="file" name="file" id="file" @change="onChangeFile" />
  </div>
</template>
<script setup>
import * as SparkMD5 from "@/assets/js/spark-md5.min.js";
const onChangeFile = async (e) => {
  console.log(e.target.files[0]);
  const chunk = sliceFile(e.target.files[0], 10 * 1024);
  let result = await hashFile(chunk);
  console.log(result);
};
/**
 *切片函数
 * @param {File} file 文件
 * @param {Number} size 切片大小
 * @returns {Array} 切片数组
 */
const sliceFile = (file, size) => {
  let result = [];
  for (let index = 0; index < file.size; index += size) {
    result.push(file.slice(index, index + size));
  }
  return result;
};
/**
 * 计算文件的md5哈希值
 * @param {Array} chunk 切片数组
 * @returns {Promise} 返回promise对象的哈希值
 */
const hashFile = (chunk) => {
  return new Promise((resolve, reject) => {
    const spark = new SparkMD5();
    function _read(i) {
      if (i >= chunk.length) {
        resolve(spark.end());
        return;
      }
      const reader = new FileReader();
      const blob = chunk[i];
      reader.onload = function (e) {
        spark.append(e.target.result);
        _read(i + 1);
      };
      reader.readAsArrayBuffer(blob);
    }
    _read(0);
  });
};
</script>
